<template>
  <div class="loginContent" style='background:#fff;height:100%' >
        <div class="loginTop">
            <div class="topBg"></div>
            <div class="logo"></div>
            <div class="loginTitle"></div>
            <!-- <div class="loginTitle"></div> -->
        </div>
        <div class="loginForm">
          <!--  -->
          <div class="tel clearfix"  v-if="!isNext">
             <span class="iocn"><img src="./images//user.png"/></span>
             <input type="tel" placeholder="" maxlength="11" v-model='phone'  style='background:none'/>
          </div>
             <div class="note clearfix"  v-if="!isNext">
            <span class="iocn"><img src="./images/iocn.png"/></span>
            <input type="number" v-model='sms' placeholder="" style='background:none' />
            <span class="getCode" @click="getCode()"></span>
          </div>
      
          <div class="password clearfix" v-if="isNext">
            <span class="iocn"><img src="./images/iocn.png"/></span>
            <input type="password" v-model="psw" placeholder=""  style='background:none'/>
          </div>
          <div class="password clearfix"  v-if="isNext">
            <span class="iocn"><img src="./images/iocn.png"/></span>
            <input type="password" v-model="newPsw" placeholder="" style='background:none' />
          </div> 

        </div>
        <div class="loginButton"  v-if="!isNext" @click="goNext()"></div>
        <div style="margin: 0px auto;text-align: center;margin-top: 20px;text-decoration: underline; color:blue;margin: 0 auto;" v-if="!isNext" @click="goLogin()"></div>
        <div class="loginButton"  v-if="isNext" @click="registerClick" ></div>
        <div  class="bottomBg"></div>
       
  </div>
</template>

<script>
import Vue from "vue";
import axiosurlService from "../../../resource/axiosurlService";
export default {
  name: "login",
  data() {
    return {
      isNext:false,
      phone: null,
      sms: null,
      psw:null,
      newPsw:null,
      openid: "",
      money: "",
      goOrder: 0,
      foodNumber: -1,
      rateCode: "",
      dts: "",
      dte: "",
      typecode: ""
    };
  },
  components: {},
  watch: {},
  methods: {
    goLogin(){
              this.$router.replace({
                path: "/login/login",
              });

    },
    goNext(){
      if(!this.phone){
        this.$toast("");
        return;
      }else if(!this.sms){
         this.$toast("");
          return;
      }else{
         this.isNext = true;
      }
    },
    getCode() {
      if (this.phone == "" || this.phone == undefined) {
        this.$toast("");
        return;
      }
      let param = {
        mobile: this.phone,
        type: 1
      }; //
      axiosurlService.getSmsCode(param).then(response => {
        let responseData = response.data;
        console.log(responseData);
        if (responseData != undefined) {
          if (responseData.success == true) {
            this.$toast("");
          } else {
            this.$toast(responseData.message);
          }
        } else {
          this.$toast("");
        }
      });
    },
    registerClick(){
      if(!this.psw||!this.newPsw){
         this.$toast("");
          return;
      }else if(this.psw!=this.newPsw){
          this.$toast("");
          return;
      }
      let param = {
        Mobile: this.phone,
        Account	: this.sms,
        Pwd: this.psw
      }; //
      axiosurlService.registerMember(param).then(response => {
        let responseData = response.data;
        if (responseData != undefined) {
          if (responseData.success == true) {
              this.$router.replace({
                path: "/houseReservation/houseReservation",
              });
          } else {
            this.$toast(responseData.message);
          }
        } else {
          this.$toast("");
        }
      });
    }
  },
  mounted() {
    
  }
};
</script>

<style lang='less' src='./less/register.less'>
</style>




