<template>

  <div class="orderContent">
     <van-tabs  @click="onClick"  v-model="active"  >
                    <van-tab :title="item.display" v-for="(item,index) in tabs" :key='index' :value="item.satate"> </van-tab>
                </van-tabs>
    <div
      class="orderAll"
      v-for="(item,index) in orderList"
      :key="index"
   
    >
      <div class="orderList clearfix"   @click.stop="goOrderDetail(item.id)">
        <div class="orderTitle clearfix" >
          <div class="fl">{{item.hotelName}}-{{item.roomTypeName}}</div>
          <div class="fr status">{{{0:'',1:'',2:'',3:'',9:'',10:'',99:''}[item.state]}}</div>
        </div>
        <div class="fl">
          <img src="./images/address.png" />
          {{item.hotelAddress}}
        </div>
        <div class="fl">
          <img src="./images/calender.png" /> 
          <span>{{item.checkinDate}}</span>&nbsp;&nbsp; 
          <span>{{item.checkoutDate}}</span>
          &nbsp;&nbsp; {{item.days}}
        </div>
      </div>
      <div class="orderButton clearfix">
        <div class="fl money">{{item.totalAmount}}</div>
        <div class="fr">
              <div class="goPay fr" v-if="item.state==9&&item.hasEvaluated==0" @click="evaulateOrder(item)"></div>
         <div class="goPay fr" v-if="item.payResult==0&&(item.state==0)"  @click="orderPay(item)"></div>      
             <div class="clear fr" v-if="item.enableCancle " @click.stop="cancleOrder(item.id)"></div>
        </div>
      </div>
    </div>   
  </div>
</template>

<script>
import Vue from "vue";
import axiosurlService from "../../../resource/axiosurlService";
export default {
  name: "index",
  data() {
    return {
      orderList: [],
      active:0,
      state:100,
      tabs:[
         {
          state:100,
          display:"",
        },
           {
          state:11,
          display:"",
        },
        // {
        //   state:0,
        //   display:"",
        // },
        //    {
        //   state:1,
        //   display:"",
        // },  
         {
          state:3,
          display:"",
        },  
         {
          state:9,
          display:"",
        }, 
          {
          state:10,
          display:"",
        },  
        // {
        //   state:101,
        //   display:"",
        // },
        {
          state:102,
          display:"",
        }, {
          state:103,
          display:"",
        },
      ]
    };
  },
  components: {},
  watch: {},
  methods: {
    goOrderDetail(id) {
      let that = this;
      that.$router.push({
        path: "/ordering/orderingUndone",
        query: {
          orderId: id
        }
      });
    },
    orderPay(item){
        let that = this;
       var OrderNo=item.orderNo;
        that.$router.push({
          path: "/pay/pay",
        query: {
          orderNo: OrderNo
        }
      });
    },
    getAllOrder(state) {
    
      let param = {
        count: 1000, //
        orderNo: null, //
        maxDate: null, //
        minDate: null, //
        state: state // //null
      }; //
      this.orderList=[];
      axiosurlService.getAllOrder(param).then(response => {
        let responseData = response.data;
        if (responseData.success == true) {
          this.orderList = responseData.data; //
        } else {
          this.$toast(responseData.message);
        }
      });
    },
         forbidback(){
          let that = this;
           that.$router.push({
             path: "/myCenter/myCenter",
           });

},
    cancleOrder(id) {
      let that = this;
      let param = {
        orderId: id //
      }; //
      axiosurlService.cancleOrder(param).then(response => {
        let responseData = response.data;
        if (responseData.success == true) {
          that.$toast("");
          that.getAllOrder(this.state);
        } else {
          that.$toast(""+responseData.message);
        }
      });
    },
    changeState(item){
        alert(item);
    },
      onClick(name, title){
        var state=0;
        this.tabs.forEach(element => {
          if(element.display==title)
          {
              state=element.state;
              this.state=state;
          }
        });
        // alert(state);
      this.getAllOrder(state);
     
        
    },
    evaulateOrder(item){
       let that = this;
       var OrderNo=item.orderNo;
      that.$router.push({
        path: "/comment/comment",
        query: {
          OrderNo: OrderNo
        }
      });
    }
  },
  mounted() {
     let state= this.$route.query.state;
     switch (state) {
       case 102:
         this.active=5;
         break;
       case 11:
         this.active=1;
         break;
       default:
         break;
     }
      history.pushState(null, null, document.URL)
        window.addEventListener('popstate', this.forbidback);
    //  if(state==102)
    //  {
    //    this.active=4;
    //  }
    this.getAllOrder(state);
  }
};
</script>

<style lang='less' src='./less/orderList.less'>
</style>




