import Vue from 'vue';
import router from '../../router'
import util from 'util'

//axion 
export const axiosRequest = function (request) {
    //
    // alert(1111);
    let config = request.headers;
    config.tenantcode  = window.localStorage.getItem('tenantcode') || '9900'; //
    config.usertoken  =window.localStorage.getItem('usertoken') ; 
    if(!navigator.onLine){
        this.$notify({ message: '', duration: 1000,background: '#1989fa'});
    }
    request.headers = config;
    return request;

}

function loadingTip() {
    util.toast('');
}
//axios 
export const axiosResponse = function (response) {
    switch (response.status) {
        case 403:
        util.toast('');
            return response;
        case 404:
        util.toast('404! !');
            return response;
        case 413:
            loadingTip()
            return response;
        case 500:
            loadingTip()
            return response;
        case 502:
            loadingTip()
            return response;
        case 503:
            loadingTip()
            return response;
        case 504:
            loadingTip()
            return response;
        default:
            if (response.status && response.status != 200) {
                loadingTip()
                return response;
            } else {
                return response
            }
    }    
}



