let host = window.location.href;
let url = host.split('/')[2];
let urlCommon = '';
let env = process.env.NODE_ENV;
console.log(env + '')
if (env == 'production') {
    //
    if (url == 'sandboxbooking.saaspms.com') {
        urlCommon = 'http://sandboxbooking.saaspms.com';//
        // urlCommon = '';//

    } else {
        //
        // urlCommon = 'http://booking.saaspms.com';//
        urlCommon = '';//
    }
}//
else if (env == 'development') {
    if(url=='localhost:8090'){ //
       //urlCommon = 'http://localhost:8088';//
         urlCommon = 'http://sandboxbooking.saaspms.com';//
    //  alert(22);
    //  urlCommon = 'http://sandboxbooking.saaspms.com';//


    } 


}
export default {
    urlCommon,
}


