<template>
  <div class="chooseContent">
    <div class="indexDate">
      <div id="checkinout">
        <div id="firstSelect" style="width:100%;">
          <div class="Date_lr" style="float:left;">
            <p></p>
            <input id="startDate" type="text" value="" style="" readonly />
          </div>
          <div class="Date_lr" style="float:right;">
            <p></p>
            <input id="endDate" type="text" value="" style="" readonly />
          </div>
          <span class="span21"><span class="NumDate">1</span></span>
        </div>
      </div>
      <div class="mask_calendar">
        <div class="calendar"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import axiosurlService from "../../../resource/axiosurlService";
import { calendarSwitch } from "./js/calenderMarketing.js";
export default {
  name: "index",
  data() {
    return {
      paras: {}
    };
  },
  components: {},
  watch: {},
  methods: {},
  created() {},
  mounted() {
    let that = this;
    let d = window.localStorage.getItem("maxdays");

    let fromPage = that.$route.query.from;
    console.log(fromPage);
    //var
    $(function() {
      $(".mask_calendar").show();

      var _minDate = window.localStorage.getItem("minDate");
      var _maxDate = window.localStorage.getItem("maxDate");
     
      
      var day = d;

      var minDate = new Date();

      if (_minDate != null && _minDate != "") {
        minDate = new Date(_minDate);
       
      }

        var months=4;
         var days =d;
      if (_maxDate != null && _maxDate != "") {
        var maxDate = new Date(_maxDate);
      

        var difftime = (maxDate - minDate) / 1000;
         var days = parseInt(difftime / 86400);
        if(days>=365){
          days=365;
        }
        if(days>=120){
          months=(days/30)+1;
        }
     
        // alert(days);
        if(months>=13){
         
          months=13;
         
        }
      }
     



      // var new_date = new Date(); //
      // var old_date = new Date("2018-12-12 00:00:00"); //"yyyy-MM-dd HH:mm:ss"

      // var difftime = (new_date - old_date)/1000; //,

      // var days = parseInt(difftime/86400); //   24*60*60*1000
      //  	var hours = parseInt(difftime/3600)-24*days;    //  60*60 -=
      //  	var minutes = parseInt(difftime%3600/60); //  -(day*24) 60   /60 
      //  	var seconds = parseInt(difftime%60);  // 60  

      //  	alert(": "+days+", "+hours+", "+minutes+", "+seconds+"");
      $("#firstSelect").calendarSwitch({
        selectors: {
          sections: ".calendar"
        },
        minDate: minDate,
        // maxDate: maxDate,
        index: months, //
        animateFunction: "slideToggle", //
        controlDay: true, //daysnumber90
        daysnumber: days, //
        comeColor: "#65708C", //
        outColor: "#65708C", //
        comeoutColor: "#E0F4F2", //
        callback: function() {
          //
          var startDate = $("#startDate").val(); //
          var endDate = $("#endDate").val(); //
          var NumDate = $(".NumDate").text(); //
          console.log(startDate);
          console.log(endDate);
          console.log(NumDate);
          let path = null;
          if (fromPage == "index") {
            path = "/index/index";
          } else if (fromPage == "houseReservation") {
            path = "/houseReservation/houseReservation";
          } else if (fromPage == "marketing") {
            // path='/ordering/ordering'
            var query = that.$route.query;
            console.log(query);
            query.startDate = startDate;
            query.endDate = endDate;
            query.days = NumDate;
            that.$router.push({
              path: "/ordering/ordering",
              query: query
            });
          }
          that.$router.replace({
            path: path,
            query: {
              startDate: startDate,
              endDate: endDate,
              days: NumDate
            }
          });
        },
        comfireBtn: ".comfire" //classid
      });
      var b = new Date();
      var ye = b.getFullYear();
      var mo = b.getMonth() + 1;
      mo = mo < 10 ? "0" + mo : mo;
      var da = b.getDate();
      da = da < 10 ? "0" + da : da;
      $("#startDate").val(ye + "-" + mo + "-" + da);
      b = new Date(b.getTime() + 24 * 3600 * 1000);
      var ye = b.getFullYear();
      var mo = b.getMonth() + 1;
      mo = mo < 10 ? "0" + mo : mo;
      var da = b.getDate();
      da = da < 10 ? "0" + da : da;
      $("#endDate").val(ye + "-" + mo + "-" + da);
    });
  }
};
</script>

<style lang='less' src='./less/calender.less'>
</style>




