<template>
    <div class="tansincontent">
        <div class="orderingUndone" v-if='orderDetail'>
            <div class="undoneTop">
                <img class="img" src="./images/bg.png"/>
                <div class="topBox">
                    <h2>{{orderDetail.hotelName}}<van-icon class="arrowR" size="12px" name="arrow" /><span class="topMoney">{{orderDetail.totalAmount}}</span></h2>
                    <p>{{orderDetail.hotelAddress}}</p>
                    <p>{{orderDetail.hotelTel}}</p>
                    <ul class="tabList">
                        <li v-if="(orderDetail.state==0||orderDetail.state==1)&&orderDetail.payResult==0" @click="cancleOrder()"></li>
                        <li v-if="orderDetail.payResult==0&&orderDetail.state==0" class="current" @click="goPay()"></li>
                    </ul>
                </div>
            </div>
            <div class="orderDetailOne">
                <!-- <div class="posi">
                    <img class="house" src="./images/house.png"/>
                    <van-cell title="" label="" />
                    <van-button class="choose"></van-button>
                </div> -->
                <van-cell title="" :value="orderDetail.roomTypeName" />
                <van-cell title="" :value="orderDetail.checkInDate" />
                <van-cell title="" :value="orderDetail.checkOutDate" />
                <van-cell v-if="orderDetail.checkInType!=1"  title="" :value="orderDetail.days" />
                <van-cell v-if="orderDetail.checkInType==1" title="" :value="orderDetail.hours" />

                <van-cell title="" :value="orderDetail.revUser" />
                <van-cell title="" :value="orderDetail.mobile" />
                <van-cell title="" :value="orderDetail.actualAmount" />
                <van-cell title="" :value="orderDetail.discountAmount" />
                <van-cell title="" :value="orderDetail.usedCoupon.couponTitle" v-if="orderDetail.usedCoupon!=null" />
                <van-cell title="" :value="orderDetail.usedCoupon.couponUseMoney" v-if="orderDetail.usedCoupon!=null" />
            </div>
            <div class="orderDetailTwo">
                <!-- <van-cell title="" :value="orderDetail.lastTime" /> -->
                <!-- <van-cell title="" value="" /> -->
                <van-cell title="" :value="orderDetail.breakfast!=0?orderDetail.breakfast+'':''" />
                <!-- <van-cell title="" value="" /> -->
                <!-- <van-cell title="" :value="orderDetail.remark" /> -->
                <van-cell title="" :value="orderDetail.orderNo" />
                <van-cell title="" :value="orderDetail.revNo" />
            </div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import { Icon,Button } from 'vant'
import axiosurlService from '../../../resource/axiosurlService'
export default {
  name: '',
   data () {
    return {
        orderDetail:null,
        orderId:null,
        orderNo:null
    }
  },
  components: {  
    
  },
  watch:{

  },
  methods: {
      cancleOrder(){
        let that = this;
        let param = {
            orderId:that.orderId, //

        }; //
        axiosurlService.cancleOrder(param).then(response => {
          let responseData = response.data;
          if (responseData.success == true) {
             that.$router.push({
                path: "/orderList/orderList",
            });
          } else {
            that.$toast(""+responseData.message);
          }
        });
      },
      getorderdetail(){
        let that = this;
        let param = {
            orderId:that.orderId, //id
        }; //
        axiosurlService.getorderdetail(param).then(response => {
          let responseData = response.data;
          if (responseData.success == true) {
            that.orderDetail = responseData.data; //banner
            that.orderNo=responseData.data.orderNo;
          } else {
            that.$toast(responseData.message);
          }
        });
      },
      goPay(id){
        let that = this;
        that.$router.push({
            path: "/pay/pay",
            query:{
                orderNo:that.orderNo
            }
        });
        },
    
     forbidback(){
          let that = this;
           that.$router.push({
             path: "/orderList/orderList",
           });

},

  },
  mounted() {
      this.orderId = this.$route.query.orderId;
       history.pushState(null, null, document.URL)
        window.addEventListener('popstate', this.forbidback);
      this.getorderdetail();
    
  }
}
</script>

<style lang='less' src='./less/orderingUndone.less'>

</style>




